.support-content{
  border-radius: 8px;
  max-width: 1290px;
  width: 77.94%;
  height: 100%;
  margin: 0 auto;
  word-break: break-all;
  .ant-card{
    border-radius: 8px;
    cursor:pointer;
    margin-bottom: 17px;
    .ant-card-body{
      padding: 24px 40px 54px 30px;
      display: flex;
      justify-content: space-between;
    }
    .ant-card-body::before{
      display: none;
    }
    .ant-card-body::after{
       display: none;
     }
    .count{
      margin-top: 15px;
      font-size: 40px;
      font-weight: bold;
      color: #040E34;
    }
    img{
      width: 46px;
      height: 46px;
    }
    .text{
      font-size: 14px;
      font-weight: 400;
      color: #9696A3;
      margin-top: 4px;
    }
  }
  .ant-card:hover{
    margin-left: 4px;
    margin-top: 4px;
    margin-bottom: 12px;
  }
  .support-list{
    background: #FFFFFF;
    opacity: 0.9;
    border-radius: 8px;
    .support-header{
     display: flex;
      padding: 16px 30px 30px 30px;
      justify-content: space-between;
      flex-wrap: wrap;
      >:first-child{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 10px;
      }
      >:last-child{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 10px;
      }
      .support-add{
        background: #5C5EE4;
        border-radius: 4px;
        font-size: 14px;
        font-weight: 400;
        color: #FFFFFF;
        text-align: center;
        margin-left: 40px;
        align-items: center;
        display: flex;
        line-height:40px;
        width: 100px;
        cursor: pointer;
        .icons{
          position: relative;
          margin-right: 8px;
          margin-left: 12px;
          .circle-hui{
            width: 8px;
            height: 8px;
            background: #FFFFFF;
            border-radius: 50%;
            opacity: 0.4;
          }
          .circle-white{
            width: 8px;
            height: 8px;
            background: #FFFFFF;
            border-radius: 50%;
            position: absolute;
            top: 3px;
            right: 2px;

          }
        }
      }
      .support-state-bold{
        font-size: 16px !important;
        font-family: Source Han Sans CN;
        font-weight: bold !important;
        color: #040E34 !important;
        margin-right: 33px;
        cursor: pointer;
      }
      .support-state{
        font-size: 14px;
        font-family: Source Han Sans CN;
        font-weight: 400;
        color: #525666;
        display: flex;
        align-items: center;
        cursor: pointer;
        margin-right: 33px;
      }
      .support-state span{
        width: 7px;
        height: 7px;
        background: #1BC5BD;
        border-radius: 50%;
        margin-right: 6px;
      }
    }
  }

  .order-add{
    .card{
      background: #FFFFFF;
      opacity: 0.9;
      border-radius: 12px;
      .title{
        padding: 40px 0px 65px 30px;
        font-size: 18px;
        font-weight: 400;
        color: #040E34;
      }
      .order-types{
        margin: 0px 40px 40px 40px;
        cursor: pointer;
        display: flex;
        flex-flow: wrap;
        .types-menu{
          flex: 1;
          margin-left: 5px;
        }

        .name{
          font-size: 14px;
          font-weight: 400;
          color: #040E34;
          margin-top: 58px;
        }
        .type-card{
          background: #FFFFFF;
          border: 1px solid #E4E6EF;
          opacity: 1;
          border-radius: 8px;
          margin-left: 16px;
          width: 114px;
          height: 134px;
          padding: 34px 0px 36px 0px;
          text-align: center;
          margin-bottom: 20px;
          display: inline-block;
          overflow: hidden;
          
          img{
            width: 30px;
            height: 32px;
            margin-bottom: 18px;
          }
          .text{
            font-size: 13px;
            font-weight: 400;
            color: #9696A3;

            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            line-clamp: 2;
            -webkit-box-orient: vertical;
          }
        }
        .type-card-active{
          background: #42C2FB;
          .text{
            color: #FFFFFF;
          }
        }
      }
      .models{
        align-items: flex-start !important;
      }
      .model{
        display: flex;
        padding: 0px 40px;
        align-items: flex-start;
        margin-top: 40px;
        .name{
          font-size: 14px;
          font-weight: 400;
          color: #040E34;
          margin-right: 25px;
          width: 56px;
          margin-top: 5px;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: normal;
        }
        .ant-input{
          background: #F2F2F7;
          border-radius: 4px;
          flex: 1;
          height: 40px;
         // line-height: 40px;
        }
        .ant-select{
          width: calc(100% - 242px);
          line-height: 40px;
          // height: 40px;
          background: #F2F2F7;
          opacity: 1;
          border-radius: 4px;
        }
        .ant-input-textarea{
          flex: 1;
        }
        .file-list{
          display: flex;
          flex-flow: wrap;
          align-items: center;
          width: calc(100% - 98px);
          .ant-upload-list{
            //display: none;
          }

          .img-item{
            margin-left: 27px;
            position: relative;
            margin-bottom: 20px;
            .item-del{
              width: 20px;
              height: 20px;
              background: #F0F0FD;
              border-radius: 50%;
              text-align: center;
              position: absolute;
              right: -10px;
              top: -10px;
              cursor: pointer;
            }
          }
          .other-item{
            margin-left: 27px;
            position: relative;
            background: #F2F2F7;
            border: 1px solid #D9DBE1;
            border-radius: 4px;
            display: flex;
            align-items: center;
            padding: 10px 12px;
            width: 132px;
            margin-bottom: 20px;
            img{
              width: 20px;
              height: 20px;
            }
            .text{
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
            }
            .item-del{
              width: 20px;
              height: 20px;
              background: #F0F0FD;
              border-radius: 50%;
              text-align: center;
              position: absolute;
              right: -10px;
              top: -10px;
              cursor: pointer;
            }
          }
          .upload-btn{
            background: #F2F2F7;
            border: 1px solid #D9DBE1;
            border-radius: 4px;
            font-size: 14px;
            font-weight: 400;
            color: #9696A3;
            display: flex;
            align-items: center;
            img{
              width: 16px;
              height: 16px;
            }
          }
        }


      }
      .tip{
        margin-left: 121px;
        margin-top: 6px;
        font-size: 12px;
        font-weight: 400;
        color: #9696A3;
      }
      .btns{
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom: 48px;
        margin-top: 50px;
        .btn-cancle{
          width: 80px;
          height: 48px;
          border: 1px solid #D9DBE1;
          font-size: 16px;
          font-weight: 400;
          line-height: 48px;
          text-align: center;
          color: #525666;
          margin-right: 12px;
          cursor: pointer;
        }
        .btn-sure{
          background: #5C5EE4;
          width: 80px;
          height: 48px;
          font-size: 16px;
          font-weight: 400;
          line-height: 48px;
          text-align: center;
          color: #FFFFFF;
          cursor: pointer;
        }
      }
      .type-list{
        display: flex;
        padding: 30px 20px 96px 24px;
        display: flex;
        .type-item{
          border: 1px solid #E4E6EF;
          padding: 30px 30px 19px 30px;
          margin-top: 16px;
          cursor: pointer;
          border-radius: 8px;
          .name{
            font-size: 18px;
            font-weight: bold;
            color: #9696A3;
          }
          .content{
            margin-top: 20px;
            font-size: 14px;
            font-weight: 400;
            color: #9696A3;
          }
        }
        .type-item:nth-child(1){
          margin-top: 0px;
        }
        .item-active{
          background: #42C2FB;
          .name{
            color: #ffffff;
          }
          .content{
            color: #ffffff;
          }
        }
        .line{
          width: 6px;
          height: 100%;
          background: #F2F2F7;
          opacity: 1;
          border-radius: 20px;

        }
      }
    }

  }
  .actions:hover .action-model{
    cursor: pointer;
    display:block;
  }
  .action-model{
    position: absolute;
    background: #FFFFFF;
    opacity: 1;
    border-radius: 8px;
    padding: 6px 10px;
    display: none;
    border: 1px solid #eeeeee;
    z-index: 100;
    width: 90px;
    div{
      ont-size: 12px;
      padding: 11px 18px;
      font-weight: 400;
      color: #5C5EE4;
    }
  }
  .status-tag{
    background: #FFE2E5;
    opacity: 1;
    border-radius: 4px;
    font-size: 12px;
    font-weight: 300;
    color: #F64E60;
    padding: 7px 10px;
    margin-left: 8px;
    width: 56px;
  }
//工单回复
