      }
      .switch-open{
        height: 26px;
        background: #F0F0FD;
        opacity: 1;
        border-radius: 20px 0px 0px 20px;
        position: absolute;
        right: 0;
        top: 8px;
        display: flex;
        align-items: center;
        padding: 6px 12px;
        cursor: pointer;
        .jiantou-left{
          width: 12px;
          height: 12px;
          background: #BDBDF4;
          border-radius: 50%;
          opacity: 1;
          position: relative;
          margin-left: 6px;
        }
        .jiantou-left:before{
          content: '<';
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%,-50%);
          color: #ffffff;
          font-size: 10px;
        }
      }
    }
    .replies{
      padding: 40px 10px 23px 30px;
